const Newsletter = () => {
  return (
    <div data-aos="zoom-in" className="bg-white">
      <div className="max-w-7xl mx-auto py-24 px-4 sm:px-6 lg:py-32 lg:px-8 lg:flex lg:items-center">
        <div className="lg:w-0 lg:flex-1">
          <h2 className="text-3xl font-extrabold text-gray-700 sm:text-4xl">
            Sign up for our newsletter
          </h2>
          <p className="mt-3 max-w-3xl text-lg text-gray-500">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Eveniet
            esse unde eligendi architecto asperiores ducimus ullam fuga adipisci
            eos aut.
          </p>
        </div>
        <div className="mt-8 lg:mt-0 lg:ml-8">
          <form className="sm:flex">
            <label htmlFor="email-address" className="sr-only">
              Email address
            </label>
            <input
              id="email-address"
              name="email-address"
              type="email"
              autoComplete="email"
              required
              className="w-full px-5 py-3 border outline-none border-pink-300 shadow-sm placeholder-pink-400 focus:ring-1 focus:ring-pink-500 focus:border-pink-500 sm:max-w-xs rounded-md"
              placeholder="Enter your email"
            />
            <div className="mt-3 rounded-md shadow sm:mt-0 sm:ml-3 sm:flex-shrink-0">
              <button
                type="submit"
                className="w-full flex items-center justify-center py-3 px-5 border border-transparent text-base font-medium rounded-md text-white bg-gradient-to-br from-pink-600 to-pink-800 hover:opacity-90 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-pink-500"
              >
                Notify me
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Newsletter;
